.response-list-container {
  display: flex;
  flex-direction: column;
  max-height: 90vh;
  min-width: 800px;
  background: #fff;

  .header {
    padding: 20px 24px;
    background: #f8f9fa;
    border-bottom: 1px solid #eee;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .title-area {
      h2 {
        margin: 0;
        font-size: 1.25rem;
        font-weight: 600;
      }
      .survey-title {
        margin: 4px 0 0;
        font-size: 0.9rem;
        color: #666;
      }
    }

    .header-actions {
      display: flex;
      gap: 12px;
      align-items: center;
    }
  }

  .content {
    padding: 24px;
    overflow: auto;
    flex: 1;

    .loading-state, .empty-state {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      padding: 60px 0;
      color: #666;

      mat-icon {
        font-size: 48px;
        width: 48px;
        height: 48px;
        margin-bottom: 16px;
        opacity: 0.5;
      }
    }

    .table-wrapper {
      width: 100%;
      overflow-x: auto;

      .response-table {
        width: 100%;
        border-collapse: collapse;
        font-size: 0.9rem;

        th {
          background: #f5f5f7;
          padding: 12px 16px;
          text-align: left;
          font-weight: 600;
          color: #1d1d1f;
          border-bottom: 2px solid #eee;
          white-space: nowrap;
        }

        td {
          padding: 12px 16px;
          border-bottom: 1px solid #eee;
          color: #48484a;

          &.time-cell {
            white-space: nowrap;
            color: #86868b;
            font-size: 0.85rem;
          }

          &.name-cell {
            font-weight: 500;
            color: #1d1d1f;
          }
        }

        tr:hover {
          background: #fafafa;
        }
      }
    }
  }
}
